.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.navbar {
  /*  */
  background: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.85));
  background-blend-mode: luminosity;
  width: 100%;
}
body {
  background-image: url("https://source.unsplash.com/UjupleczBOY ");
  background-position-x: center;
  font-family: "Lato", sans-serif;
  background-size: cover;
  /* background-blend-mode: luminosity; */

  /* background-color:rgb(255, 255, 255); */
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

#license {
  color: black;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#block {
  background-color: rgba(255, 255, 255, 0.4);
}
h1 {
  font-family: "Abril Fatface", cursive;
}
img {
  width: 100%;
  height: 100%;
}
#ppImg {
  width: 190px;
  height: 250px;
  object-fit: contain;
}
.error-message {
  width: 100%;
  height: auto;
  display: inline-block;
  padding: 5px;
  background: rgba(255, 0, 0, 0.459);
  color: #fff;
  text-align: center;
  margin: 0.5rem 0;
}
.success-message {
  width: 100%;
  height: auto;
  display: inline-block;
  padding: 5px;
  background: rgba(9, 255, 0, 0.589);
  color: #fff;
  text-align: center;
  margin: 0.5rem 0;
}
